.services {
  margin-top: 50px;

  & h2 {
    margin-left: 0;
    text-align: center;
  }
}

.services__list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 40px;
}

.services__item {
  margin-bottom: 30px;
  padding: 40px;

  background-color: $basic-grey;
  background-repeat: no-repeat;
  background-position: 95% 10%;
  border-radius: 5px;
}

.services__item--loyalty,
.services__item--connection {
  width: 48%;
}

.services__item--loyalty {
  background-image: url("../img/pig.svg");
}

.services__item--connection {
  background-image: url("../img/arrows.svg");
}

.services__item--analize {
  margin-bottom: 0;
  width: 100%;
  background-position: right center;
  background-image: url("../img/man.webp");
}

.services__item--analize .services__item-subheader {
  width: 60%;
}

.services__item-header {
  margin-bottom: 20px;

  font-weight: 600;
  font-size: 20px;
}

.services__item-subheader {
  font-size: 15px;
  line-height: 28px;
  color: $special-black;
  margin-bottom: 0;
}

@media screen and (max-width: 992px) {
  .services__item--loyalty,
  .services__item--connection {
    background-size: 50px auto;
  }

  .services__item--analize {
    background-position: right center;
  }

  .services__item--analize .services__item-subheader,
  .services__item--analize .services__item-header {
    width: 50%;
  }
}

@media screen and (max-width: 768px) {
  .services {
    margin-top: 40px;
  }

  .services__list {
    flex-direction: column;
  }

  .services__item {
    width: 100%;
  }

  .services__item--loyalty,
  .services__item--connection {
    background-size: 60px auto;
  }

  .services__item--analize .services__item-subheader,
  .services__item--analize .services__item-header {
    width: 100%;
  }

  .services__item--analize {
    padding-bottom: 250px;
    background-image: url("../img/man-tablet.webp");

    background-position: bottom center;
  }
}

@media screen and (max-width: 350px) {
  .services__item--loyalty,
  .services__item--connection {
    padding: 20px;
    background-size: 50px auto;
  }

  .services__item-header {
    font-size: 18px;
  }

  .services__item--analize {
    padding: 20px;
    padding-bottom: 220px;
  }
}
