.application {
  margin-top: 50px;

  background-color: $basic-grey;

  & h2 {
    margin-left: 0;
  }
}

.application__wrapper {
  padding: 50px 0;
  display: flex;
}

.application__subtitle {
  font-size: 15px;
  line-height: 28px;
  color: $special-black;
}

.application__btn {
  width: 250px;
  margin-left: 0;
}

@media screen and (max-width: 991px) {
  .application__wrapper {
    padding-top: 50px;

    & .application__wrapper-img {
      order: 2;
      margin-top: 10px;
    }
  }

  .application__subtitle {
    width: 100%;
  }
}

@media screen and (max-width: 767px) {
  .application__wrapper {
    margin-top: 40px;
    padding: 40px 0;

    background-position: center 90%;
    background-size: 100%;

    & .application__wrapper-img {
      order: 2;
      margin-top: 0;
    }
  }

  .application__subtitle {
    width: 100%;
  }
}

@media screen and (max-width: 576px) {
  .application__wrapper {
    padding-top: 40px;
  }
}

@media screen and (max-width: 480px) {
  .application__wrapper {
    background-size: 100%;
  }
}
