.request {
  margin-top: 50px;
  padding: 50px 0;

  background-color: $basic-grey;

  & h2 {
    margin-left: 0;
  }
}

.request__form-input {
  width: 100%;
  height: 50px;
  margin-bottom: 20px;
  padding-left: 15px;

  font-size: 14px;

  border: none;
  border-color: $basic-white;
  border-radius: 5px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1) inset;

  &:hover {
    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

.request__form-btn {
  margin-top: 20px;
  margin-left: 0;

  border: none;
}

.request__subtitle {
  font-size: 15px;
  line-height: 28px;
  color: $special-black;
}

@media screen and (max-width: 991px) {
  .request__wrapper {
    display: flex;
  }

  .request__subtitle {
    margin-bottom: 40px;
  }

  .request__form {
    order: 2;
    width: 100%;
    margin-top: 50px;

    & button {
      margin-bottom: 0;
    }
  }

  .request__title {
    & br {
      display: none;
    }
  }
}

@media screen and (max-width: 767px) {
  .request {
    margin-top: 40px;
    padding: 40px 0;
  }
}
