.main-screen {
  position: relative;
  padding: 50px 0;
}

.main-screen--wrapper {
  display: flex;
  align-items: center;
}

.main-screen__title {
  font-weight: 600;
  font-size: 64px;
}

.modal-header .modal-title {
  text-align: center;
}

.main-screen__subtitle {
  margin-top: 30px;

  font-size: 16px;
  line-height: 26px;
  color: $special-black;
}

.main-button {
  display: block;
  width: 200px;
  margin-top: 50px;
  padding: 15px 40px;

  font-size: 14px;
  text-align: center;
  color: $basic-black;
  text-decoration: none;

  background-color: $basic-yellow;
  border-radius: 5px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1);

  &:hover {
    color: $special-black;

    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

@media screen and (max-width: 1200px) {
  .main-screen__title,
  .main-button,
  .main-screen__subtitle {
    margin-left: 0;
  }
}

@media screen and (max-width: 991px) {
  .main-screen {
    padding: 40px 0;
  }

  .main-screen__title,
  .main-button,
  .main-screen__subtitle {
    margin-left: 0;
  }

  .main-button {
    margin-bottom: 50px;
  }
}

@media screen and (max-width: 767px) {
  .main-screen__title,
  .main-button,
  .main-screen__subtitle {
    margin-left: 0;
  }
}

@media screen and (max-width: 540px) {
  .main-screen__title,
  .main-button,
  .main-screen__subtitle {
    margin-left: 0;
  }
}

@media screen and (max-width: 500px) {
  .main-screen__title {
    font-size: 48px;
  }
}
