.page-header {
  padding: 10px 0;

  background-color: $basic-grey;
}

.page-header__logo {
  margin-right: 50px;
}

.navbar-toggler-icon {
  background-image: url("../img/menu.webp");
  background-repeat: no-repeat;

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

.main-nav__list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
}

.user-nav {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
}

.user-nav__link--sign-in,
.user-nav__link--sign-up {
  color: $special-grey;
  text-decoration: none;

  &:hover {
    color: $basic-black;

    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

.user-nav__link--sign-in {
  padding-left: 20px;

  background-image: url("../img/user.svg");
  background-repeat: no-repeat;
  background-position: left center;
}

.user-nav__link--sign-up {
  padding-left: 25px;

  background-image: url("../img/user-in.svg");
  background-repeat: no-repeat;
  background-position: left center;
}

.navbar-collapse {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: auto;
}

.user-nav__item {
  margin-right: 20px;
  padding: 10px 15px;

  font-size: 14px;

  background-color: $basic-white;
  border-radius: 5px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.05);

  &:last-child {
    margin-right: 0;
  }
}

.site-list__link {
  margin-right: 30px;

  font-size: 14px;
  color: $special-grey;
  text-decoration: none;

  &:hover {
    color: $special-grey;

    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

.navbar-expand-lg {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.socials {
  @include reset-list;

  display: flex;
  align-items: baseline;
}

.socials__item {
  margin-right: 5px;
  margin-bottom: -5px;
}

.socials__link {
  &:hover {
    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

@media screen and (max-width: 991px) {
  .main-nav__list {
    width: 100%;
  }

  .socials {
    width: 100%;
    margin-top: 30px;
  }

  .user-nav {
    width: 100%;
    margin-top: 40px;
  }

  .navbar-collapse {
    display: flex;
    flex-direction: column;
    margin-top: 30px;
  }

  .user-nav__item {
    margin-bottom: 30px;

    font-size: 16px;
  }

  .site-list__link {
    font-size: 16px;
  }

  .site-list__item {
    margin-bottom: 20px;
  }
}
