.page-footer {
  padding: 30px 0;

  background-color: $basic-light-grey;
}

.footer {
  @include reset-list;

  display: grid;

  grid-gap: 30px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.page-footer__text {
  margin-bottom: 10px;

  font-size: 15px;
  color: $special-black;

  & a {
    text-decoration: none;
    color: $basic-black;

    &:hover {
      color: $basic-black;
      opacity: 0.8;
    }

    &:focus {
      outline: none;
      opacity: 0.8;
    }
  }
}

.page-footer__copyright {
  font-size: 14px;
  color: $special-black;
}

@media screen and (max-width: 992px) {
  .socials--footer {
    margin-top: 10px;
  }

  .page-footer__copyright {
    font-size: 13px;
  }
}

@media screen and (max-width: 768px) {
  .footer {
    grid-gap: 30px;
    grid-template-columns: 1fr 1fr;
  }

  .page-footer__copyright {
    font-size: 15px;
  }

  .page-footer__text {
    font-size: 16px;
  }
}

@media screen and (max-width: 576px) {
  .footer {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 400px) {
  .footer {
    grid-template-columns: 1fr;
  }
}
