.advantages {
  margin-top: 50px;

  & h2 {
    margin-left: 0;
    text-align: center;
  }
}

.advantages__subtitle {
  display: block;
  width: 70%;
  margin: 0 auto;

  font-size: 15px;
  line-height: 28px;
  text-align: center;
  color: $special-black;

  & span {
    font-weight: 600;
  }
}

.advantages__list {
  @include reset-list;

  display: flex;
  justify-content: space-between;
  margin-top: 40px;
}

.advantages__item {
  width: 23%;
  padding-top: 90px;

  font-size: 15px;
  text-align: center;
  color: $special-black;

  background-repeat: no-repeat;
  background-position: top center;
}

.advantages__item--cards {
  background-image: url("../img/cards.webp");
}

.advantages__item--coins {
  background-image: url("../img/coins.webp");
}

.advantages__item--people {
  background-image: url("../img/people.webp");
}

@media screen and (max-width: 992px) {
  .advantages__list {
    justify-content: space-between;
  }

  .advantages__item {
    width: 30%;
  }
}

@media screen and (max-width: 768px) {
  .advantages {
    margin-top: 40px;
  }

  .advantages__list {
    flex-direction: column;
    align-items: center;
  }

  .advantages__item {
    width: 50%;
    margin-bottom: 40px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .advantages {
    margin-top: 40px;
  }

  .advantages__subtitle {
    width: 95%;
  }

  .advantages__item {
    width: 80%;
  }
}
