.modal-input {
  width: 60%;
  display: block;
  margin: 0 auto;
  padding: 10px 15px;

  font-size: 14px;

  border: 1px solid $basic-light-grey;
  border-radius: 5px;

  &:hover {
    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

.modal-input-checkbox {
  width: auto;
}

.modal-label {
  width: 100%;
  font-size: 14px;
  margin-right: 20px;
  margin-left: 10px;
}

.modal-btn {
  padding: 10px 15px;

  font-size: 14px;
  color: $basic-black;

  background-color: $basic-yellow;
  border: none;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1);
  border-radius: 5px;

  &:hover {
    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

.modal-footer {
  display: flex;
  flex-direction: column;
}

.modal-footer__wrapper {
  display: flex;
  align-items: center;
}

.modal-link {
  text-align: center;
  margin-right: 10px;

  font-size: 14px;
  color: $special-black;
  text-decoration: none;

  &:hover {
    color: $special-black;

    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

.close {
  width: 18px;
  height: 18px;

  background-color: transparent;
  background-image: url("../img/close.png");
  background-repeat: no-repeat;
  background-position: center;
  border: none;

  &:hover {
    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}
