.about-us {
  background-color: $basic-grey;
}

.about-us--wrapper {
  padding: 50px 0;
  display: flex;
}

.title {
  margin-bottom: 20px;
  margin-right: 0;

  font-weight: 600;
  font-size: 30px;
}

.about-us__subtitle {
  font-size: 15px;
  line-height: 28px;
  color: $special-black;

  &:last-child {
    margin-bottom: 0;
  }
}

@media screen and (max-width: 991px) {
  .about-us--wrapper {
    padding-left: 0;

    & .about-us--wrapper-img {
      margin-top: 50px;
      order: 2;
    }
  }

  .title {
    margin-left: 0;
  }

  .about-us__subtitle {
    width: 100%;
    margin-left: 0;
  }
}

@media screen and (max-width: 767px) {
  .about-us--wrapper {
    padding: 40px;
    padding-left: 0;

    & .about-us--wrapper-img {
      margin-top: 30px;
      order: 2;
    }
  }

  .title {
    margin-left: 0;
  }

  .about-us__subtitle {
    width: 100%;
    margin-left: 0;
  }
}

@media screen and (max-width: 500px) {
  .title {
    font-size: 25px;
  }
}
